<template>
	<view>
		<navbar :type="1" :avatar="avatar"></navbar>
		<scroll-view scroll-y="true" >
			<view class="messageContent">
				<u-swipe-action :show="item.show" :index="index"
							v-for="(item, index) in messageList" :key="index" 
							 @click="deleteClick" @open="open" @content-click="goChat(index)"
							:options="options" btn-width="140"
						>
						<view class="messageItem" :class="active==index?'active':''">
							<view class="messageItem-left">
								<image :src="item.avatar" class="messageItem-img"></image>
								<u-badge type="error" :count="item.count" :offset="[-10,-10]"></u-badge>
							</view>
							<view class="messageItem-main">
								<view class="messageItem-name">
									{{item.name}}
								</view>
								<view class="messageItem-message">
									{{item.message}}
								</view>
							</view>
							<view class="messageItem-time">{{item.time | filtedate}}</view>
						</view>
					</u-swipe-action>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import { formatDate } from '@/static/js/dateFormat.js';
	export default {
		data() {
			return {
				avatar:'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
				messageList:[{
					id: 1,
					count: 2,
					avatar: 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
					name: '好友请求',
					message: '奇石云根浅第三代',
					time: 1614689655857
				},{
					id: 5,
					count: 1,
					avatar: 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
					name: '梵蒂冈',
					message: '奇石云根浅第三代',
					time: 1614551181585
				},{
					id: 6,
					count: 4,
					avatar: 'http://browser9.qhimg.com/bdm/800_450_85/t010769fafba59eb5c4.jpg',
					name: '萨达',
					message: '奇石云根浅第三代',
					time: 161431161100
				}],
				active:-1,
				show: false,
				// 左滑取消框内数据
				options: [
					{
						text: '删除',
						style: {
							backgroundColor: '#dd524d'
						}
					}
				],
			};
		},
		methods:{
			back(){
				uni.navigateBack();
			},
			goChat(index){
				this.active = index;
				uni.navigateTo({
					url:'../chat/chat'
				})
			},
			// 点击确定左滑删除
			deleteClick(index, index1) {
				this.messageList.splice(index, 1);
				this.$chat.alert(`删除了第${index}个课程`,'none');
			},
			// 如果打开一个的时候，不需要关闭其他，则无需实现本方法
			open(index) {
				// 先将正在被操作的swipeAction标记为打开状态，否则由于props的特性限制，
				// 原本为'false'，再次设置为'false'会无效
				this.messageList[index].show = true;
				this.messageList.map((val, idx) => {
					if(index != idx) this.messageList[idx].show = false;
				})
			}
		},
		filters: {
			filtedate: function(date) {
				let t = new Date(date);
				return formatDate(t, 1);
			}
		}
	}
</script>

<style lang="scss">
.messageContent{
	padding: 20rpx 0;
	.messageItem{
		width: 100%;
		height: 150rpx;
		padding: 0 30rpx;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		justify-content: space-between;
		.messageItem-left{
			position: relative;
			.messageItem-img{
				width: 100rpx;
				height: 100rpx;
				border-radius: 10px;
			}
		}
		.messageItem-main{
			width: 430rpx;
			height: 90rpx;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			white-space: nowrap;
			overflow: hidden;
			.messageItem-name{
				font-size: 16px;
				font-weight: bold;
			}
			.messageItem-message{
				font-size: 14px;
			}
		}
		.messageItem-time{
			width: 112rpx;
			text-align: right;
			font-size: 12px;
			color: #999;
			margin-bottom: 40rpx;
		}
	}
	.active{
		background-color: #eee;
	}
}
</style>
